.education_title {
    padding: 2rem;
    background-color: var(--color-bg-light);
    display: flex;
    flex-direction: row;
}

.education_title h1 {
    font-size: 25.11px;
    line-height: 30.30px;
    font-family: var(--font-family);
    color: var(--color-text-header);
    cursor: pointer;
}

.education_title_bar {
    border: var(--border);
    width: 100%;
    height: 2px;
    flex: 1;
    margin-left: 1rem;
    margin-top: 1rem;
}

.education_content {
    padding-top: 2rem;
    padding-bottom: 4rem;
    background-color: var(--color-bg-light);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

.education_content-image{
    float: 1;
    margin-left: 2rem;
    margin-right: 2rem;
}

.education_content-detail {
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
}

.education_content-image img{
    width: 450px;
    height: 400px;
    border-radius: 1rem;
}

.education_content-detail-line {
    margin-right: 2rem;
    width: 1px;
    height: full;
    border: var(--border);
}

@media screen and (max-width: 820px) {
    .education_content {
        flex-direction: column;
    }
    .education_content-image{
        display: none;
    }
    .education_content-detail-line {
        margin-right: 0rem;
        margin-left: 2rem;
    
    }
    
  }
  
  @media screen and (max-width: 490px) {
    .education_content {
        flex-direction: column;
    }
    .education_content-image{
        display: none;
    }
    .education_content-detail-line {
        margin-right: 0rem;
        margin-left: 1rem;
    
    }
  }

  