

.about_me-title {
    padding: 2rem;
    background-color: var(--color-bg-light);
    display: flex;
    flex-direction: row;
}

.about_me-content {
    padding: 2rem;
    background-color: var(--color-bg-light);
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    align-items: center;
}

.about_me-content-image {
    width: 400px;
    height: 500px;

}

.about_me-content-image img{
    width: 100%;
    height: 100%;
    border-radius: 1rem;
}

.about_me-content-description {
    max-width: 600px;
    max-height: 600px;
    padding: 70px 10px 0 10px;
    text-align: center;
}

.about_me-content-description p{
    text-align: left;
    padding: 20px 0;
    font-family: var(--font-family);
    color: var(--color-text-2);
    font-size: 18px;
    line-height: 1.5rem;
    position: relative;
}

.about_me-title h1 {
    font-size: 25.11px;
    line-height: 30.30px;
    font-family: var(--font-family);
    color: var(--color-text-header);
    cursor: pointer;
}

.about_me-title_bar {
    border: var(--border);
    width: 100%;
    height: 2px;
    flex: 1;
    margin-left: 1rem;
    margin-top: 1rem;
}

@media screen and (max-width: 820px) {
    .about_me-content {
        padding: 2rem;
        flex-direction: column-reverse; 
    }
    .about_me-content-description p{
        text-align: center;
    }
    .about_me-content-image img{
        width: 100%;
        height: 100%;
    }

    .about_me-content-description {
        width: 100%;
        padding: 10px 10px 10px 10px;
        padding-bottom: 2rem;
    
    }

    .about_me-content-image{
        width: 400px;
        height: 500px;

    }

    
}

@media screen and (max-width: 550px) {
    .about_me-content {
        padding-left: 1rem;
        padding-right: 1rem;
        flex-direction: column-reverse; 
    }

    .about_me-content-description p{
        text-align: center;
    }

    .about_me-content-image{
        width: 200px;
        height: 250px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .about_me-content-image{
        margin: auto;
    }

    .about_me-content p{
        padding: 10px 0;
        width: 100%;
        font-size: 16px;
    }
    
    .about_me-content-description {
        padding: 0rem;
        padding-bottom: 2rem;
        width: 100%;
        text-align: center;
    }
}